// weui cells 
$weuiCellBg:#FFFFFF;
$weuiCellBorderColor:#D9D9D9;
$weuiCellGapV:pxTorem(10px);
$weuiCellGapH:pxTorem(24px);
$weuiCellInnerGapH:pxTorem(35px);
$weuiCellHeight: pxTorem(44px);
$weuiCellFontSize:pxTorem(32px);
$weuiCellTipsFontSize:pxTorem(24px);
$weuiCellLabelWidth:pxTorem(105px);
$weuiCellLineHeight: ($weuiCellHeight - 2 * $weuiCellGapV) / $weuiCellFontSize;
$weuiCellsMarginTop:pxTorem(16px);


// wei_cell
.weui_cell {
    position: relative;
    &:before {
        @include setTopLine($weuiCellBorderColor);
        left: $weuiCellGapH
    }
    &:first-child {
        &:before {
            display: none
        }
    }
}

.weui_cells {
    margin-top: $weuiCellsMarginTop;
    background-color: $weuiCellBg;
    line-height: $weuiCellLineHeight;
    font-size: $weuiCellFontSize;
    overflow: hidden;
    // onepx
    position: relative;
    &:before {
        @include setTopLine($weuiCellBorderColor);
    }
    &:after {
        @include setBottomLine($weuiCellBorderColor);
    }
}

.weui_cells_title {
    margin-top: .77em; // 15px - 行高
    margin-bottom: .3em; // 8px - 行高
    padding-left: $weuiCellGapH;
    padding-right: $weuiCellGapH;
    color: $color-txt-6;
    font-size: $weuiCellTipsFontSize;
    &+.weui_cells {
        margin-top: 0;
    }
}

.weui_cells_tips {
    margin-top: .3em; // 8px - 行高
    color: $color-txt-6;
    padding-left: $weuiCellGapH;
    padding-right: $weuiCellGapH;
    font-size: $weuiCellTipsFontSize;
}


.weui_cell {
    padding: $weuiCellGapV $weuiCellGapH;
    position: relative; //这个是为了兼容cells容器onepx方案被before挡住而做的
    display: flex;
    align-items: center;
}


.weui_cell_hd {
    width: pxTorem(46px);
    height: pxTorem(46px);
    margin-right: pxTorem(20px);
    display: block;
    line-height: pxTorem(46px);
    text-align: center;
}
.weui_cell_bd_tips{
    margin: 0 0 pxTorem(20px) 0;
    line-height: pxTorem(24px);
    font-size: $weuiCellTipsFontSize;
    color: $color-txt-9;
}

.weui_cell_ft {
    text-align: right;
    font-size: $weuiCellFontSize * .9;
    color: $color-txt-3;
}

.weui_cell_primary {
    flex: 1;
}

.weui_cells_access {
    .weui_cell:not(.no_access) {
        // 在cell_access和其它类型的cell混着用的场景下，其它cell要加no_access，避免有点击态
        @include setTapColor();
        &:active {
            background-color: #ECECEC;
        }
    }
    .weui_cell_bd {
        line-height:pxTorem(80px);
        p {
            @include text-ellipsis(1);
        }
    }
    a.weui_cell {
        color: $color-txt-3;
        min-height: pxTorem(100px);
    }
    .weui_cell_ft {
        color: $color-txt-9;
        min-width: 5%;
        margin-right: -.1rem;
        p {
            margin-top: pxTorem(20px);
            @include text-ellipsis(1);
        }
        i {
            height: 100%;
            line-height: 100%;
            margin-top: .01rem
        }
        // &:after {
        //     content: " ";
        //     // .setArrow_Wap(right, 6px, #C8C8CD, 2px);
        //     // top: -1px;
        //     margin-left: pxTorem(30px);
        // }
    }
}
